.footer-color {
  background-color: $secondary-bcg;
}

.flex-footer {
  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: space-between;
    padding-bottom: 97px;
  }
}

.tadlet-footer {
  @media screen and (min-width: 768px) {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}

.description__title {
  font-family: Titan One;
  font-size: 18px;
  line-height: 1.16;
  text-transform: uppercase;
  margin-bottom: 17px;
  width: 232px;
  color: $main-paragraf-txt;

  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 1.15;
    letter-spacing: 0.04em;
    width: 340px;
  }

  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 1.13;
    width: 100%;
  }
}

.description__heading {
  font-family: Titan One;
  font-size: 34px;
  line-height: 1.14;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  margin-bottom: 43px;
  width: 200px;
  white-space: pre-wrap;
  color: $main-titles-txt;

  @media screen and (min-width: 768px) {
    font-size: 48px;
    letter-spacing: 0.06em;
    margin-bottom: 0;
    width: 340px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 58px;
    line-height: 1.13;
    width: 100%;
  }
}

.social {
  margin-bottom: 46px;
}

.social-item {
  display: flex;
  justify-content: flex-end;
}

.social-icons {
  fill: $icon-media;
}

.call {
  display: block;
  font-family: DM Sans;
  font-weight: bold;
  font-size: 12px;
  text-align: end;
  margin-bottom: 43px;
  @media screen and (min-width: 768px) {
    font-size: 14px;
  }

  @media screen and (min-width: 1280px) {
    font-size: 16px;
  }
}

.call__adds {
  color: $primary-txt;
}
.call__number {
  font-style: normal;
  line-height: 1.83;

  color: $main-titles-txt;
}

hr {
  position: absolute;
  top: auto;
  left: 0;
  width: 100%;
  color: $reviews-line-color;

  border: none;
  background-color: $reviews-line-color;
  height: 1px;
}

.creator {
  font-family: DM Sans;
  font-weight: bold;
  font-size: 14px;

  padding-top: 22px;
  padding-bottom: 25px;

  text-align: center;
  color: $secondary-txt;

  @media screen and (max-width: 767px) {
    width: 150px;
    margin-left: auto;
    margin-right: auto;
  }
}
